<template>
	<view>
		<cu-custom bgColor="theme-bg-color" :isBack="true">
			<block slot="backText"><text>返回</text></block>
			<block slot="content"><text> 掌握情况 </text></block>
		</cu-custom>
		<scroll-view scroll-x class="bg-white nav">
			<view class="flex text-center">
				<view class="cu-item flex-sub" :class="index==TabCur?'text-green cur':''"
					v-for="(item,index) in labelItem" :key="index" @tap="tabSelect" :data-id="index">
					{{item.name}}
				</view>
			</view>
		</scroll-view>
		
		<view v-if="knowItem.length>0" class="cu-card article" :class="true?'no-card':''">

			<template v-for="(item,index) in knowItem">
				<view class="cu-item shadow" @click="ToDetail(item)">
					<view class="title">
						<view class="text-cut">{{item.name}}</view>
					</view>
					<view class="content">
						<image :src="item.imageUrls|pictureFilter" mode="aspectFill"></image>
						<view class="desc">
							<view class="text-content"> {{item.shortDescription}}</view>
							<view>
								<view class="cu-tag bg-red light sm round">{{item.creationTimeDateBeforeNow}}</view>
								<view class="cu-tag bg-green light sm round">热点:{{item.vistorCount}}</view>
							</view>
						</view>
					</view>
				</view>
			</template>
		</view>
		<view v-else class="solids-bottom padding-xs flex align-center bg-white">
			<view class="flex-sub text-center">

				<view style="min-height:100vh" class="padding">相关内容还没有哦~~~</view>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				labelItem:[
					{name:"了解",id:"2"},
					{name:"熟悉",id:"3"},
					{name:"掌握",id:"4"},
					],
				TabCur:0,
				knowItem: [],
			}
		},
		onShow() {
			this.GetMyMasterLevel();
		},
		methods: {
			async GetMyMasterLevel() {
				let url = "/WxApi/KnowLedgeWx/GetMyMasterLevel";

				this.$comm.ShowLoading();
				let {
					success,
					data: {
						items
					}
				} = await this.$http.PostAsync(url, {id:this.labelItem[this.TabCur].id});
				if (success == true) {
					this.knowItem = items;
				}
				uni.hideLoading()
			},
			async tabSelect(e) {
				this.TabCur = e.currentTarget.dataset.id;
				this.scrollLeft = (e.currentTarget.dataset.id - 1) * 60
				await this.GetMyMasterLevel();
				
				
			},
			async ToDetail({
				id
			}) {
				uni.navigateTo({
					url: `/pages/knowLedge/knowLedgeDetail?id=${id}`
				})
			}

		}
	}
</script>

<style>

</style>
